/* flash.css */
.flash-container {
  position: absolute;
  left: 50%;
  margin-left: -250px;
  top: 10%;
  background: white;
  color: rgb(198, 79, 79);
  -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.09), 0 6px 6px rgba(0, 0, 0, 0.13);
          box-shadow: 0 10px 20px rgba(0, 0, 0, 0.09), 0 6px 6px rgba(0, 0, 0, 0.13);
  width: 500px;
  height: 50px;
  text-align: center;
  z-index: 100;
  padding-top: 20px;
  padding-bottom: 20px;
